@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{font-size:62.5%}*{margin:0;padding:0;box-sizing:border-box}body{color:white;background-color:#464646;font-family:'Heebo', sans-serif;font-size:1.6rem}button{background-color:transparent;border:none;border-radius:0;font-size:1.6rem}

.settings-navbar{display:flex;justify-content:space-between;align-items:center;width:100%;padding:3.2rem 2.8rem}.settings-navbar__left{display:flex;align-items:center}.settings-navbar__left>*{margin:0 1rem}.settings-navbar__name{font-size:2.5rem}

.button-bubble{display:flex;justify-content:center;align-items:center;width:4.8rem;height:4.8rem;background-color:white;border-radius:100%;cursor:pointer;transition:all 0.2s ease}.button-bubble:hover{opacity:0.8}.button-bubble.active{color:white;background-color:#1e95ac;position:relative}.button-bubble.active::before{content:'';position:absolute;width:100%;height:100%;top:0;left:0;box-sizing:border-box;border:1rem solid #326e79;border-radius:100%;-webkit-transform:scale(1);transform:scale(1);-webkit-transform-origin:center;transform-origin:center;-webkit-animation:bubble 0.3s linear forwards;animation:bubble 0.3s linear forwards}@-webkit-keyframes bubble{from{-webkit-transform:scale(1);transform:scale(1);opacity:1}to{-webkit-transform:scale(1.4);transform:scale(1.4);opacity:0}}@keyframes bubble{from{-webkit-transform:scale(1);transform:scale(1);opacity:1}to{-webkit-transform:scale(1.4);transform:scale(1.4);opacity:0}}

.settings-home__container{display:flex;justify-content:center}.settings-home__form{max-width:60%;margin-top:10rem}.settings-home .input-text{margin-bottom:2rem}.settings-home .row{display:flex;flex-wrap:wrap;padding:0 1rem}.settings-home .col{flex:1 1;margin:0 1rem}.settings-home .divider{display:block;height:2px;width:100%;background-color:white;opacity:0.2;border-radius:1px;margin:2rem 0}

.input-text{display:flex;flex-direction:column}.input-text__label{color:white;font-size:1.4rem;margin-bottom:0.6rem}.input-text__input{height:3rem;border-radius:0.5rem;background-color:white;font-size:1.6rem;padding:0 1rem}

.input-range__slider{display:flex;justify-content:space-between;font-size:1.2rem}.input-range__slider *{padding:1rem}.input-range input[type='range']{-webkit-appearance:none;width:100%;background:transparent}.input-range input[type='range']::-webkit-slider-thumb{-webkit-appearance:none}.input-range input[type='range']:focus{outline:none}.input-range input[type='range']::-ms-track{width:100%;cursor:pointer;background:transparent;border-color:transparent;color:transparent}.input-range input[type='range']::-webkit-slider-thumb{-webkit-appearance:none;margin-top:-5px;border:2px solid #464646;height:14px;width:14px;border-radius:100%;background:#ffffff;cursor:pointer}.input-range input[type='range']::-moz-range-thumb{border:2px solid #464646;height:14px;width:14px;border-radius:100%;background:#ffffff;cursor:pointer}.input-range input[type='range']::-ms-thumb{border:2px solid #464646;height:14px;width:14px;border-radius:100%;background:#ffffff;cursor:pointer}.input-range input[type='range']::-webkit-slider-runnable-track{width:100%;height:0.4rem;cursor:pointer;background:white;border-radius:1000px;border:none}.input-range input[type='range']:focus::-webkit-slider-runnable-track{background:#1e95ac}.input-range input[type='range']::-moz-range-track{width:100%;height:0.4rem;cursor:pointer;background:white;border-radius:1000px;border:none}.input-range input[type='range']::-ms-track{width:100%;height:8.4px;cursor:pointer;background:transparent;border-color:transparent;border-width:16px 0;color:transparent}.input-range input[type='range']::-ms-fill-lower{background:#2a6495;border:0.2px solid #010101;border-radius:2.6px;box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d}.input-range input[type='range']:focus::-ms-fill-lower{background:#1e95ac}.input-range input[type='range']::-ms-fill-upper{background:#1e95ac;border:0.2px solid #010101;border-radius:2.6px;box-shadow:1px 1px 1px #000000, 0px 0px 1px #0d0d0d}.input-range input[type='range']:focus::-ms-fill-upper{background:#1e95ac}

.settings-colors__form{display:grid;grid-gap:2rem;gap:2rem;grid-template-columns:repeat(auto-fit, minmax(10em, 16em));padding:0 3.2rem;margin-bottom:5rem}.settings-colors .input-text{margin-bottom:2rem}.settings-colors .input-checkbox{font-weight:700}.settings-colors .row{display:flex;flex-wrap:wrap;padding:0 1rem}.settings-colors .col{flex:1 1;margin:0 1rem}.settings-colors .divider{display:block;height:2px;width:100%;background-color:white;opacity:0.2;border-radius:1px;margin:2rem 0}

.input-checkbox{display:inline-flex;align-items:center;margin-right:4rem;cursor:pointer;-ms-user-select:none;user-select:none;-moz-user-select:none;-webkit-user-select:none}.input-checkbox__checkbox{display:inline-flex;justify-content:center;align-items:center;height:3.8rem;width:3.8rem;border-radius:100%;margin-right:1rem;position:relative}.input-checkbox__checkbox__label{font-weight:700}.input-checkbox input[type='checkbox']{display:none}

.settings-plate__form{display:grid;grid-gap:2rem;gap:2rem;grid-template-columns:repeat(auto-fit, minmax(10em, 16em));padding:0 3.2rem;margin-bottom:5rem}.settings-plate .input-text{margin-bottom:2rem}.settings-plate .input-checkbox{font-weight:700}.settings-plate .row{display:flex;flex-wrap:wrap;padding:0 1rem}.settings-plate .col{flex:1 1;margin:0 1rem}.settings-plate .divider{display:block;height:2px;width:100%;background-color:white;opacity:0.2;border-radius:1px;margin:2rem 0}

.tray{display:flex;position:fixed;bottom:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.tray .button-bubble{margin:2rem 3rem}.tray .inactive,.tray .inactive:hover{opacity:0.4 !important;cursor:default !important;pointer-events:none !important}.tray__middle{display:flex;border-radius:3.2rem 3.2rem 0 0;background-color:#b3bbbe;padding:1rem}.tray__middle .button-bubble,.tray__middle .separator{margin:1rem}

.toolbar{position:fixed;right:1.6rem;top:10rem}

.toolbar-group{display:flex;flex-direction:column;border-radius:1000px;background-color:#b3bbbe;margin-bottom:1.6rem;padding:0.5rem}.toolbar-group .button-bubble{margin:0.5rem}

.popup{display:none;padding:1rem;background-color:#b3bbbe;border-radius:3.2rem;position:absolute}.popup.open{display:inline-flex;justify-content:center;min-width:50vw;right:0;top:-50%;-webkit-transform:translateX(-8rem);transform:translateX(-8rem)}.popup_body{display:flex;justify-content:space-between;flex-wrap:wrap}

.app-title{display:inline-flex;justify-content:center;align-items:center;background-color:#1e95ac;width:50rem;height:11rem;border:0.5rem solid #326e79;border-radius:0 0 3.2rem 3.2rem;margin-top:-0.5rem;font-size:4rem;font-weight:700;position:relative}

.add{cursor:url(data:image/png;base64,) 8 8,auto}.paint{cursor:url(data:image/png;base64,) 8 8,auto}.dropper{cursor:url(data:image/png;base64,) 8 8,auto}.delete{cursor:url(data:image/png;base64,) 8 8,auto}.rotate{cursor:url(data:image/png;base64,) 8 8,auto}

